.SQtZc {
    position: fixed;
    left: 0px;
    top: 0px;
    z-index: 1000;
    width: 100%;
    background-color: rgb(255, 133, 42);
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: 0;
    -webkit-tap-highlight-color: #e2191933;
}
a {
    text-decoration: none;
}
header {
    display: block;
}
div {
    display: block;
}
Style Attribute {
    font-size: 16px;
}
.SQtZc .header-bd {
    width: 100%;
    padding: 6px 9px 6px 42px;
}
.SQtZc .header-bd .placeholder {
    display: block;
    font-size: 0px;
    background-color: rgb(255, 78, 34);
    text-align: center;
    border-radius: 4px;
}
.SQtZc .header-bd .placeholder .text {
    height: 25px;
    line-height: 25px;
    font-size: 13px;
    display: inline-block;
    color: rgb(240, 224, 220);
}
.SQtZc::before {
    font-size: 27px;
    color: rgb(255, 255, 255);
    margin-left:7px;
    margin-top:3px;
    float: left;
}
.recommend-hd {
    padding: 12px 0;
    width: 100%;
    text-align: center;
}
.recommend-hd-bg {
    width: 161px;
    height: 19px;
}
.recommend-container{
    width: 375px;
    height: 1060px;
}